.header {
  background-color: rgba(25, 34, 33, .62);
  padding: 10px;
}

.form {
  max-width: 400px;
  gap: 5px;
  margin: 0 auto;
  display: flex;
}

.form__input {
  width: 270px;
  padding: 5px 5px 5px 20px;
}

.form__btn {
  flex-grow: 1;
}

.input__wrapper {
  position: relative;
}

.icon {
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: #000;
  fill: #000;
  z-index: 10;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 3px;
  transform: translateY(-50%);
}

.go-back {
  position: absolute;
  bottom: 0;
}

.main {
  padding-top: 10px;
  padding-bottom: 10px;
}

.list {
  gap: 10px;
  display: grid;
}

@media (min-width: 425px) {
  .list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px) {
  .list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1280px) {
  .list {
    grid-template-columns: repeat(5, 1fr);
  }
}

.list__item img {
  aspect-ratio: 2 / 2.2;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

